import { Component, OnInit } from '@angular/core';
import {ControllerService} from '../services/controller.service';
import {Router} from '@angular/router';
import {ConstantsService, PRIVILEGES} from '../services/constants.service';

@Component({
  selector: 'app-my-order',
  templateUrl: './my-order.page.html',
  styleUrls: ['./my-order.page.scss'],
})
export class MyOrderPage implements OnInit {

  id = location.pathname.replace('/my-order/', '');
  order = this.api.getOrderById(this.id);
  cart = this.api.getCart();

  constructor(private api: ControllerService, private router: Router, private currency: ConstantsService) { }

  ngOnInit() {
  }

  deleteOrder(id: string) {
    this.api.deleteOrder(id);
    location.assign('/my-orders');
  }

}
